import request from './request.js'

let baseUrl = 'https://api.mianku.vip/' //  

let $http = new request({
	// 
	baseUrl: baseUrl,
	// 
	headers: {
		// 'Content-Type': 'application/x-wwwform-urlencoded',
		'content-type': 'application/json'
	},
	// 
	// --
	isPrompt: false,
	// 
	load: true,
	// 
	isFactory: false,
	// 
	loadMore: true
	
})
// 
let requestNum = 0
// 
$http.requestStart = function (options) {
	if (requestNum <= 0) {
		if (options.load) {
			// 
			// uni.showLoading({
			// 	title: '',
			// 	mask: true
			// })
		}
	}
	requestNum += 1
	// token
	options.headers['token'] = uni.getStorageSync('token')
	return options
}
// 
$http.requestEnd = function (options, resolve) {
	// 
	requestNum = requestNum - 1
	if (requestNum <= 0) {
		// uni.hideLoading()
	}
	// console.log('resolve: ',resolve)
	// if (resolve.errMsg && (resolve.errMsg != 'request:ok' && resolve.errMsg != 'uploadFile:ok' || resolve.statusCode && resolve.statusCode !== 200)) {
	// 	uni.showToast({
	// 		title: '',
	// 		icon: 'none'
	// 	})
	// }
}
export default $http